import React, { useState } from "react";
// Material UI
import { Tabs, Tab, Divider, Grow, Paper } from "@material-ui/core";

// components
import ItemOne from "./tabs/ItemOne";
import ItemTwo from "./tabs/ItemTwo";
import ItemThree from "./tabs/ItemThree";

const Work = () => {
  const [value, setValue] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Grow in={true} style={{ transformOrigin: "0 0 0" }} timeout={2000}>
      <Paper
        style={{
          flexGrow: 1,
          display: "flex",
          // margin: "-60px 30px 0px"
          margin: "-61px 30px 0px",
          boxShadow:
            "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)",
        }}
      >
        <Tabs
          orientation="vertical"
          indicatorColor="primary"
          variant="scrollable"
          value={value}
          onChange={handleChange}
          aria-label="Work Vertial Tabs"
        >
          <Tab label="Item 1" />
          <Tab label="Item 2" />
          <Tab label="Item 3" />
          <Tab label="Item 4" />
        </Tabs>
        <Divider orientation="vertical" flexItem />
        {value === 0 && <ItemOne />}
        {value === 1 && <ItemTwo />}
        {value === 2 && <ItemThree />}
      </Paper>
    </Grow>
  );
};

export default Work;
