import React from "react";
// Material UI
import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  useScrollTrigger,
  makeStyles,
} from "@material-ui/core";
// React Router
import { Link } from "react-router-dom";
// Component
import { RouteData } from "./routes";
// Images
import logo from "../../asset/logo.png";
import logoTransparent from "../../asset/logo_transparent.png";

const useStyles = makeStyles(() => ({
  root: {
    // backgroundColor: "#cccccc",
    borderRadius: "3px",
  },
  iconButtonLabel: {
    display: "flex",
    flexDirection: "column",
  },
}));

const NavigationBar = () => {
  const trigger = useScrollTrigger({
    threshold: 270,
    disableHysteresis: true,
  });
  const classes = useStyles();
  return (
    <>
      <AppBar
        position="fixed"
        className={classes.root}
        style={{ backgroundColor: trigger ? "#fff" : "transparent" }}
      >
        <Toolbar variant="dense">
          {/* <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="menu"
          >
            <img
              src={trigger ? logo : logoTransparent}
              style={{
                maxWidth: "100px",
                // margin: "10px 0px 0px 10px"
              }}
            />
          </IconButton> */}
          <Typography variant="h6" style={{ flexGrow: 1 }}></Typography>
          {RouteData.map((route) => (
            <IconButton
              key={route.title}
              color={trigger ? "#555555" : "inherit"}
              to={route.path}
              component={Link}
              aria-label={route.title}
              classes={{
                label: classes.iconButtonLabel,
              }}
            >
              {route.icon}
              <div style={{ fontSize: "10px" }}>{route.title}</div>
            </IconButton>
          ))}
        </Toolbar>
      </AppBar>
    </>
  );
};

export default NavigationBar;
