import React, { useRef, useEffect } from "react";
import { Paper, Typography, Zoom } from "@material-ui/core";
import ProfileImg from "../../asset/profile-pic.jpg";
// TypedJs
import Typed from "typed.js";

const Home = () => {
  const typeNameTarget = useRef(null);
  const typeTitleTarget = useRef(null);
  const typeContentTarget = useRef(null);

  useEffect(() => {
    const typedName = new Typed(typeNameTarget.current, {
      strings: ["Tue Tran"],
      typeSpeed: 40,
      showCursor: false,
      startDelay: 2000,
    });
    const typedTitle = new Typed(typeTitleTarget.current, {
      strings: ["Software Engineer"],
      typeSpeed: 40,
      showCursor: false,
      startDelay: 3000,
    });
    const typedContent = new Typed(typeContentTarget.current, {
      strings: [
        "Based in Bentonville, AR specializing in front-end and back-end technologies.",
      ],
      typeSpeed: 40,
      showCursor: false,
      startDelay: 4000,
    });
    return () => {
      typedName.destroy();
      typedTitle.destroy();
      typedContent.destroy();
    };
  }, []);

  return (
    <Zoom in={true} timeout={2000}>
      <Paper
        elevation={3}
        style={{
          display: "flex",
          alignItems: "center",
          flexDirection: "column",
          margin: "-61px 30px 0px",
          boxShadow:
            "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)",
        }}
      >
        <img
          alt="Profile Pic"
          src={ProfileImg}
          style={{ borderRadius: "50%", transform: "translate3d(0, -50%, 0)" }}
        />
        <br />
        <div style={{ marginTop: "-125px", textAlign: "center" }}>
          <Typography
            variant="h3"
            style={{
              color: "#3C4858",
              fontWeight: 700,
            }}
            ref={typeNameTarget}
          ></Typography>
          <Typography
            variant="h5"
            style={{
              color: "#3C4858",
              fontWeight: 700,
            }}
            ref={typeTitleTarget}
          ></Typography>
          <Typography
            variant="h6"
            style={{
              color: "#3C4858",
              fontWeight: 500,
            }}
            ref={typeContentTarget}
          ></Typography>
        </div>
      </Paper>
    </Zoom>
  );
};

export default Home;
